.App {
  text-align: center;
  margin-top: 1rem;
  font-size: 0.8rem;
  height: 100vh;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
.col-flex{
  display: flex;
}
.col-flex-margin{
  margin-block-start: auto;
}
.noUi-connect {
  background-color:#ff6170;
}
#red .noUi-connect {
  background-color:#ff6170;
}
#blue .noUi-connect {
  background-color:#6db4ff;
}
#yellow .noUi-connect {
  background-color:#f5fb6a;
}
#green .noUi-connect {
  background-color:#28a745;
}
#aqua .noUi-connect {
  background-color:aqua;
}
#state td {
  vertical-align: middle;
}
input[type='number'] {
  -moz-appearance:textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
.rule-container {
    padding: 1rem;
    border: .2rem solid #ececec;
    border-radius: 8px;
    height: 90%;
    overflow: hidden;
    overflow-y: scroll;
    overflow-x: hidden;
}
.btn-b{
  bottom: 0;
  position: absolute;
}
.sm-text {
  font-size: smaller;
}
tbody:hover tr {
  color: #212529;
  background-color: rgba(0, 0, 0, 0.075);
}
.p1 {
  padding: 1rem;
}
.ruleset-container {
  padding: 1rem;
  border: .2rem solid #cccaca;
  border-radius: 8px;
}
.noUi-horizontal {
  height: 8px;
  margin-top: 0.7rem;
}
.noUi-horizontal .noUi-handle {
  height: 20px;
}
.noUi-handle:before, .noUi-handle:after {
  top: 3px;
}
.noUi-horizontal .noUi-tooltip {
  bottom: 0%;
}
.noUi-tooltip {
  padding: 3px;
  font-size: 0.6rem;
}
.padding0 {
  padding: 0px;
}
.table thead th {
  vertical-align: top;
}
/*переключатель слайдера*/
.noUi-handle {
  border: none;
    border-radius: inherit;
    background: none;
    cursor: default;
    box-shadow: none;
}
.noUi-handle:before, .noUi-handle:after {
  display: none;
  
}
/**/
/*Табы*/
.nav-link {
  padding: 0.4rem 0.3rem;
}
/**/
/*размер input*/
.form-control-smm{
  height: 30px;
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem;
  line-height: 1.5;
  border-radius: 0.2rem;
}
.input-group-smm > .custom-select {
  padding: 0rem 0.5rem;
  font-size: 0.875rem;
    line-height: 1.5;
    border-radius: 0.2rem;
}
.input-group-smm > .custom-select {
  padding-right: 1.75rem;
}
.custom-select {
  width: 3rem;
}
/**/